.host-comp {
	@include flex-end;
	width: 100%;
}

.hostPicture {
	display: flex;
	margin-left: 10px;
	width: 32px;
	height: 32px;
	border-radius: 50%;
	background: #c4c4c4;

	@media screen and (min-width: $breakpoint-desktop) {
		width: 64px;
		height: 64px;
	}

	& img {
		@include WH-full;
		border-radius: 50%;
		object-fit: cover;
	}
}

.host-name {
	@include flex-end;
	width: 63px;
	height: 66px;
	text-align: right;
	@include font(12px, 500);

	@media screen and (min-width: $breakpoint-desktop) {
		@include font(18px, 500);

	}
}

.rate-host-container {
	display: flex;
	flex-direction: row-reverse;
	justify-content: space-between;

	@media screen and (min-width: $breakpoint-desktop) {
		flex-direction: column;
		justify-content: center;
	}
}

.hostCard-container {
	display: flex;
	flex-direction: column;
	width: 100%;
	justify-content: space-between;

	@media screen and (min-width: $breakpoint-desktop) {
		flex-direction: row;
		align-items: flex-end;
	}
}

.tagContainer {
    display: flex;
}

.tagButton {
    @include flex-center;
    background: $primary-color;
    color: $tertiary-color;
    margin-top: 4%;
    @include font(10px, 500);
    height: 18px;
    border-radius: 5px;
    margin-right: 10px;
    padding: 2px 10px 0 10px;

    @media screen and (min-width: $breakpoint-desktop) {
        flex-direction: column;
        height: 25px;
        @include small-radius;
        @include font(14px, 500);
    }
}

.flatInfos-container{
	@include flex-column;

    h2 {
        margin: 2% 0;
        @include font(18px, 500);

        @media screen and (min-width: $breakpoint-desktop) {
            @include font(36px, 500);
        }
    }

    h3 {
        margin: 2% 0;
        @include font(14px, 500);

        @media screen and (min-width: $breakpoint-desktop) {
            @include font(18px, 500);
        }
    }
}

.tags-container {
    display: flex;
    flex-wrap: wrap;
    align-items: baseline;
}

.profile-container {
	display: flex;
	flex-direction: row-reverse;
	justify-content: space-between;
	@media screen and (min-width: $breakpoint-desktop) {
		flex-direction: column;
	}
}